.tag {
  display: flex;
  margin-top: 20px;
  flex-wrap: wrap;
  gap: 10px;
}

.tag h3 {
  margin: 0;
  padding: 5px 25px;
  background-color: #ff6060;
  color: white;
  border-radius: 25px;
  margin-right: 10px;
  width: fit-content;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-weight: 400;
  font-size: 0.9em;
}
